import "./wines.css";
import Modal from "../Modal/Modal.js";
import{useState} from "react";
import boca  from "./slika-boca.jpg";
function Wines(){


  
  const [visible,setVisble]= useState(false);
  function handleClick(){
         
      setVisble(true);
  }
 
    return(
      <div className="wines">
        <Modal isOpen={visible} handleClose={()=>{setVisble(false)}}/>
         
          <div className="grid-container">
            <div className="wine1" onClick={handleClick}>
              
              <p>Traminac vrhunski</p>
            </div>
            <div className="wine2" onClick={handleClick}>
              
              <p>Frankovka kavalitetna</p>
            </div>
            <div className="wine3" onClick={handleClick}>
              
              <p>Frankovka vrhunska</p>
            </div>
            <div className="wine4" onClick={handleClick}>
              
              <p>Graševina kvalitetna</p>
            </div>
            <div className="wine5" onClick={handleClick}>
              
              <p> Graševina vrhunska</p>
            </div>
            <div className="wine6" onClick={handleClick}>
              
              <p>Rose</p>
            </div>
          </div>
      </div>


    )
}
export default Wines;