
import {Link} from "react-router-dom";
import "./style-nav.css";
import {useState,useEffect} from "react";

function Navbar(){
  const[active,setActive]=useState(false);
  const[clicked,setClicked]=useState(false);
  const[burger,setBurger]=useState("burger");
  
useEffect(()=>{
  if(clicked){
    setBurger("burger clicked")}
else{
  setBurger("burger closed");
}
},[clicked]
)

 function handleClick (e){
    setClicked(!clicked)
    setActive(!active);
 }


    return(
        
        <header>
        <div id="navigation">
          <p id="logo">Logo</p>
          <nav className={active ? "transition activated":""}>
            <ul>
              <li className={active? "activated":""}><Link to="/" onClick={active?handleClick:null}>Home</Link></li>
              <li className={active? "activated":""}><Link to="/wines" onClick={active?handleClick:null}>Wines</Link></li>
              <li className={active? "activated":""}><Link to="/contact" onClick={active?handleClick:null}>Contact</Link></li>
            </ul>
          </nav>
          <div  onClick={handleClick} className={burger}>
            <div id="line1" ></div>
            <div id="line2" ></div>
            <div id="line3" ></div>
        </div>
        </div>
      </header>
        

    )

}

export default Navbar;