import logo from "./logo-pahulja2.png";
import vineglass from "./vine-glass.png";
import "./style-home.css";
import {useState, useEffect, useRef} from "react";


function useOnScreen(ref, options) {
    // State and setter for storing whether element is visible
    const [isIntersecting, setIntersecting] = useState(false);
  
    useEffect(() => {
      const observer = new IntersectionObserver(
        ([entry],observer) => {
          // Update our state when observer callback fires
         
          setIntersecting(entry.isIntersecting);
        },options
        
      );
      if (ref.current) {
        observer.observe(ref.current);
      }
      return () => {
         if (ref.current) {
            observer.unobserve(ref.current);
          }
      };
    }, []); // Empty array ensures that effect is only run on mount and unmount
  
    return isIntersecting;
  }

const Home= ()=>{

    const ref= useRef();
    const secondref = useRef();
    const options={
        root:null,
        threshold:0.5,
        rootMargin:"0px"
    }

    const firstSection = useOnScreen(ref,options);
    const secondSection = useOnScreen(secondref,options);
  

    return(
        <>
        <div className="home">
        <section className="landing">
        <img src={logo} alt="logo" id="logo-img"/>
        </section>
        <section className="about-wine" ref={ref}>
        <article style={firstSection?{opacity:"100"}:{}}>
            <div className="text">
            <h2>Wine</h2>
            <p id="wine-text">Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident voluptatem cumque veritatis beatae, et dolore aut unde error aspernatur voluptates quam! Eum voluptatum tempora repellat culpa tempore ab modi odit?</p>
            </div>       
        </article>
        <article style={firstSection?{opacity:"100"}:{}}>
            
            <img src={vineglass} alt="vineglass"/>
            
        </article>
        </section>
        <section className="about-vineyard" ref={secondref}>
            <article style={secondSection?{opacity:"100"}:{}}>
            <div className="text">
            <h2>Vineyard</h2>
            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident voluptatem cumque veritatis beatae, et dolore aut unde error aspernatur voluptates quam! Eum voluptatum tempora repellat culpa tempore ab modi odit?</p>
            </div>    
                </article>
            <article style={secondSection?{opacity:"100"}:{}}>
                <img src="" alt="" srcSet="" />
            </article>
        </section>
        </div>
        </>
    )
}

export default Home;