import { useState, useEffect } from "react";
import slika from "./slika-boca.jpg";
import "./style-modal.css";



function Modal({isOpen,handleClose}){
   if(!isOpen) return null;

return(
<div className="background">
    <div className="modal">
    <button onClick={handleClose} id="close">x</button>
        <div className="info">
        <img src={slika} alt=""/>
        <div className="info-text">
            <h3>Ime vina</h3>
            <h5>Karakteristike</h5>
            <p>-prvakarakteistika <br/>
               -druga karakteristika</p>
             <h5>Proizvodnja</h5>
             <p>opis proizvodnje</p>
             <h5>Okus</h5>
        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Magni voluptas exercitationem velit, molestiae adipisci recusandae, odit deleniti laborum sed perferendis nulla tempora quod amet, et possimus obcaecati aliquam? Aut, voluptas?</p>
        </div>
        </div>
    </div>
    </div>
)



}

export default Modal;